@import url(https://fonts.googleapis.com/css2?family=Merienda+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

body {
    background: rgb(187,187,237);
    background: linear-gradient(90deg, rgba(187,187,237,1) 1%, rgba(142,139,203,1) 64%, rgba(152,237,255,1) 100%);
}

.display {
    margin-top: 80px;
    max-width: 1100px;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent;
    justify-content: center;
    flex-wrap: wrap;
}

.weather-box-container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1100px;
    flex-wrap: wrap;
}

.add-new-button {
    background-color: grey;
    opacity: 0.6;
    transition: ease-in 0.3s;
    width: 200px;
    border: 3px solid black;
}

.add-new-button:hover {
    opacity: 1;
}

.add-new-container {
    display: flex;
    max-width: 1100px;
    flex-wrap: wrap;
    height: 600px;
    justify-content: center;
    align-items: center;
}
.delete-button {
    background-color: red;
    width: 100%;
    height: 30px;
    border-radius: 10px;
    opacity: 0.6;
    transition: ease-in 0.3s;
}

.delete-button:hover {
    border: 1px solid black;
    opacity: 1;
}

.weather-info-container-0 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    padding: 5px;
    margin: 5px;
    width: 200px;
    height: 300px;
    border-radius: 10px;
    background: rgb(178,163,249);
    background: linear-gradient(90deg, rgba(178,163,249,1) 0%, rgba(206,218,255,1) 50%, rgba(178,163,249,1) 100%);   
}

.weather-info-container-0-20 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    padding: 5px;
    margin: 5px;
    width: 200px;
    height: 300px;
    border-radius: 10px;
    background: rgb(242,180,196);
    background: linear-gradient(90deg, rgba(242,180,196,1) 0%, rgba(221,229,255,1) 49%, rgba(242,180,196,1) 100%);
}

.weather-info-container-20 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    padding: 5px;
    margin: 5px;
    width: 200px;
    height: 300px;
    border-radius: 10px;
    background: rgb(246,78,121);
    background: linear-gradient(90deg, rgba(246,78,121,1) 0%, rgba(239,221,255,1) 49%, rgba(246,78,121,1) 100%);
}

.text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    font-family: 'Merienda One', cursive;
}

.error-container {
    margin-top: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    width: 100%;
    height: 100%;
    font-family: 'Merienda One', cursive;
}

.location {
    font-size: 24px;
}

.main-temp {
    font-size: 30px;
}

.sub-temp {
    font-size: 14px;
}

.form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    width: 500px;
    height: 500px;
}

.search-bar-container {
    display: flex;
    justify-content: center;
}

.search-bar-container input{
    width: 100%;
    height: 50px;
    border-radius: 50px;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    border: 3px solid black;

}

.buttons-container {
    display: flex;
    justify-content: space-around;

}

button {
    margin: 10px;
    width: 120px;
    height: 40px;
    border-radius: 50px;
    color: white;
    font-size: 16px;
}

.cancel-button, .search-button {
    background-color: grey;
    opacity: 0.7;
    border: 1px solid black;
    transition: ease-in 0.3s;
}

.search-button:hover, .cancel-button:hover {
    opacity: 1;
    border: 2px solid black;
}

button:focus, input:focus {
    outline: none;
} 

input:focus::-webkit-input-placeholder {
    color: transparent;
}
